import React, {useState, useEffect, useRef} from 'react';
import styled from 'styled-components';
import ListItem from './listItem';
import axios from 'axios';
import {MdSwapHoriz, MdExpandMore} from 'react-icons/md';
import ReactCountryFlag from 'react-country-flag';
import Spinner from './spinner';
import currencyToSymbolMap from 'currency-symbol-map/map'
import {config} from '../config';

function Select(props) {
    const [toggle, setToggle] = useState({toggleSelling: false, toggleBuying: false});
    const [rate, setRate] = useState(props.initialRate)
    const [left, setLeft] = useState(`1.00`)
    const [right, setRight] = useState((Number(left)*rate).toFixed(2))
    const [selling, setSelling] = useState({ flag: `GB`, currencyCode: `GBP`, currency: `British Pounds`})
    const [buying, setBuying] = useState({flag: `EU`,currencyCode: `EUR`, currency: `Euro`}) 
    const listRef = useRef()    
    
    const listSelectHandler = (key, exchange) => {
        if(exchange === `buying`) { 
             setBuying({flag: key.slice(0,2),currencyCode: key, currency: config[key].name})
             setToggle(prevToggle => ({toggleSelling: prevToggle.toggleSelling, toggleBuying: false}))
        } else {
             setSelling({flag: key.slice(0,2),currencyCode: key, currency: config[key].name})
             setToggle(prevToggle => ({toggleSelling: false, toggleBuying: prevToggle.toggleBuying}))
        }
    }   

    const swapHandler = () => {
        setBuying(selling)
        setSelling(buying) 
    }

    useEffect(() => {
        const fetchData = async () => {
            let a = selling.currencyCode;
            let b = buying.currencyCode
            const result = await axios(
                `https://api.exchangeratesapi.io/latest?base=${a}&symbols=${b}`
            )
            setRate(Object.values(result.data.rates)[0])
        } 
        fetchData();
        setRight((left*rate).toFixed(2))
    },[selling,buying,rate,left])

    const handleClick = (e) => {
        console.log(e,listRef.current)
        if(e.target === listRef){
            setToggle({toggleSelling: false, toggleBuying: false}); 
        }
    }

    useEffect(() => {
        document.addEventListener(`mousedown`, handleClick, false)
    //    return document.removeEventListener(`mousedown`, handleClick, false)
    },[]) 
         
    return (
         <SelectWrapper>
            <SelectContainer>
                <ReactCountryFlag style={flagStyle} svg countryCode={selling.flag}/>
                <div className='currency-tags'>
                    <p>{selling.currencyCode}</p>
                    <p>{selling.currency}</p>
                </div>
                <MdExpandMore 
                    style={{transform: toggle.toggleSelling ? `rotate(180deg)` : `rotate(0deg)`, height: `60px`}} 
                    onClick={() => setToggle(prevToggle => ({toggleSelling: !prevToggle.toggleSelling, toggleBuying: prevToggle.toggleBuying}))}/>
                 <DropDownContainer ref={listRef} toggle={toggle.toggleSelling}>
                     {props.rateData.map( e => (
                         <ListItem 
                            key={Math.random()}
                            clicked={() => listSelectHandler(e,`selling`)}
                            code={e}
                         />
                     ))}
                </DropDownContainer>                
            </SelectContainer>
            <SwapButton>
                <MdSwapHoriz onClick={swapHandler}/>
            </SwapButton>   
            <SelectContainer>
                <ReactCountryFlag style={flagStyle} className='flag'svg countryCode={buying.flag}/>
                <div className='currency-tags'>
                    <p>{buying.currencyCode}</p>
                    <p>{buying.currency}</p>
                </div>
                <MdExpandMore 
                    style={{transform: toggle.toggleBuying ? `rotate(180deg)` : `rotate(0deg)`, height: `60px`}} 
                    onClick={() => setToggle(prevToggle => ({toggleSelling: prevToggle.toggleSelling, toggleBuying:!prevToggle.toggleBuying}))}/>
                <DropDownContainer  ref={listRef} toggle={toggle.toggleBuying}>
                    {props.rateData.map( e => (
                        <ListItem 
                            key={Math.random()*10}
                            clicked={() => listSelectHandler(e,`buying`)}
                            code={e}
                        />
                    ))}
                </DropDownContainer>
            </SelectContainer>
            {rate === undefined ? <><Spinner/><Spinner/></> : 
                <>
                   <InputContainer>
                   <span>{currencyToSymbolMap[selling.currencyCode]}</span>
                   <input  type="text"  value={left}
                       onChange={(e) => {
                           setLeft(e.target.value)
                           setRight(((e.target.value)*rate).toFixed(2))
                       }}/>
                 </InputContainer>
                  <InputContainer>
                    <span>{currencyToSymbolMap[buying.currencyCode]}</span>
                    <input  type="text"  value={right}
                        onChange={(e) => {
                            setLeft(((e.target.value)/rate).toFixed(2))
                            setRight(e.target.value)
                        }}/>
                  </InputContainer>
                </>
            }
        </SelectWrapper>
    )
}


const flagStyle = {
    width: `50px`,
    height: `100%`,
}

const SwapButton = styled.div`
   svg {
        width: 50px;
        height: 100%;
        transition: 0.3s;
    &:hover {
        color: blue;
        cursor: pointer;
    }
    &:active {
        transform: rotate(180deg);
    }
   }
`


const SelectWrapper = styled.div`
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
`

const SelectContainer = styled.div`
    position: relative;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-start;
    flex-direction: row;
    width: 270px;
    height: 60px;
    border-radius: 5px;
    border: none;
    background: white;
    padding: 10px;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    .currency-tags{
        width: fit-content;
        padding-left: 20px;
        display: flex;
        flex-direction: column;
        p:first-of-type{
            font-size: 20px;
            font-weight: bold;
            margin: 0;
        }
        p{
            font-size: 14px;
            font-weight: regular;
            letter-spacing: 1.2px;
        }
    }
    svg{
        position: absolute;
        align-self: center;
        width: 30px;
        right: 15px;
        transition: 0.3s;
        &:hover{
            cursor: pointer;
            transform: scale(1.2);
        }
    }
`
const DropDownContainer = styled.div`
    position: absolute;
    top: 60px;
    left: 0;
    width: 270px;
    overflow: scroll;
    z-index: 100;
    background: none;
    border-radius: 5px;
    transition: 0.2s;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    height: ${props => props.toggle ? `240px` : `0px` };
`
const InputContainer = styled.div`
    display: flex;
    margin-top: 10px;
    border-radius: 5px;
    width: 270px;
    height: 60px;
    background: white;
    flex-direction: row;
    align-items: center;
    padding: 10px;
    font-size: 20px;
    font-weight: 400;
    font-family: 'Monospace';
    span {
        width: 30px;
        padding-right: 5px;
    }
    input { 
        width: 250px;
        height: 60px;
        justify-content: space-evenly;
        border: none;
        font-size: 20px;
        font-weight: 400;
        font-family: 'Monospace';
    &:focus{
        outline: none;
    }
   }
`
export default Select;